html,
body {
  font-family: "TT Lakes";
  font-weight: 500; }

* {
  box-sizing: border-box; }

.menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 320px;
  height: 100%;
  box-sizing: border-box;
  box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2), -2px 0px 4px rgba(69, 79, 126, 0.02);
  color: #1b1c21;
  overflow: scroll;
  z-index: 10; }
  .menu::-webkit-scrollbar {
    display: none; }
  .menu__header {
    width: 100%;
    padding: 18px 0; }
  .menu__navigation {
    margin-top: 46px;
    margin-bottom: 32px; }
  .menu__footer {
    margin-top: auto;
    margin-bottom: 32px; }

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0 24px; }

.button {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  background-color: #ff3e79;
  background-repeat: no-repeat;
  background-position: center center;
  transition: 0.25s;
  transition-property: background-color;
  cursor: pointer; }
  .button:hover {
    background-color: #da1651; }
  .button--close {
    background-image: url(/img/menu/header/burger-close.svg); }
  .button--search {
    background-image: url(/img/menu/header/search.svg); }
  .button--phone {
    background-image: url(/img/menu/footer/phone.svg); }
  .button--chat {
    background-image: url(/img/menu/footer/chat.svg); }
  .button--profile {
    background-image: url(/img/menu/footer/profile.svg); }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .header > * {
    display: block; }
  .header__logo {
    margin-left: 16px;
    margin-right: auto; }

.navigation__list {
  padding: 0;
  margin: 0;
  list-style: none; }
  .navigation__list-item {
    position: relative;
    height: 24px;
    margin-bottom: 24px;
    display: block; }
    .navigation__list-item:hover a:before {
      content: "";
      display: block;
      height: 32px;
      width: 4px;
      position: absolute;
      top: -4px;
      left: -24px;
      background: url(/img/menu/highlight.svg); }

.navigation__link {
  color: #1b1c21;
  text-decoration: none;
  line-height: 24px; }
  .navigation__link:hover {
    color: #7e7e82; }

.footer .activity {
  display: flex; }
  .footer .activity__button {
    margin-right: 16px; }

.address {
  font-style: normal; }
  .address__email a {
    color: #1b1c21;
    text-decoration: none; }
  .address__phone {
    margin: 0 0 40px 0;
    font-size: 24px;
    font-weight: bold; }

.locali__option {
  cursor: pointer; }

.locali__input {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; }

.locali input:checked + .locali__name {
  color: #7e7e82; }
